// para que el documento este listo, el html se haya descargado y los archivos js
document.addEventListener('DOMContentLoaded', function() {
    crearGaleria();
});

function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes') //seleccionar el elmnto galeria-imagenes

    for(let i =1; i<=12; i++) { //itear sobe todas las imagenes
        const imagen = document.createElement('IMG');   //generar la imagen
        
        imagen.src = `build/img/thumb/${i}.webp`; //de donde va a tomar el src para crear la img, dependiendo d la posiscion i
        imagen.dataset.imagenId = i; //le agrega el id, forma de crea atributos personalizados

        //aÃ±adir la funcion mostrarImagen
        imagen.onclick = mostrarImagen;

        // crear el li para mostrarla en el html 
        const lista = document.createElement('LI');
        // agegar la imagen
        lista.appendChild(imagen);

        // agregar los li a galeria
        galeria.appendChild(lista);
    }
}

// funacion para mostrar imagen en grande
// cuando le das un clic sobre la imagen
function mostrarImagen(e) {
    // console.log(e.target); //trae el <...> con el elmento
    // console.log(e.target.dataset.imagenId); //trae el <...> con el NUM DEL ELEMENTO 5 EN STRING

    // convetir el string a num
    const id = parseInt(e.target.dataset.imagenId);
    // console.log(id); // 4 en numero
    
    // crear imagen que esta grande 
    const imagen = document.createElement('IMG');
    imagen.src = `build/img/grande/${id}.webp`;



    // crear variable con un div para el ovelay par agregarle la img
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen);
    overlay.classList.add('overlay') //agregar clase para editar en _galeria.scss 

    // cuando se da click cerar la imagen
    overlay.onclick = function() {
        overlay.remove();
    }

    // boton para cerrar la img
    const cerrarImagen = document.createElement('P');
    cerrarImagen.textContent ='X';
    cerrarImagen.classList.add('btn-cerrar');

    // cuando se ppresiona x, se cierra la imagen
    cerrarImagen.onclick = function() {
        overlay.remove(); //quita la imagen que esta en el overlay
    }

    // agrregarlo al html
    overlay.appendChild(cerrarImagen);


    // mostrarlo en el html, seleccionando la etiqueta body, para agregar la imagen
    const body = document.querySelector('body');
    body.appendChild(overlay);
    body.classList.add('fijar-body'); // la clase se define en _globales
    
};