// registrar eventlistener para el document

// cuando este listo el doc se ejecuta la funcion
document.addEventListener('DOMContentLoaded', function() {
    scrollNav(); 

    navegacionFija();
});

// APLICA EL SCROLL PARA PASAR D EUNA SECCION A OTRA DENSDE EL HTML
function scrollNav() { 
    const enlaces = document.querySelectorAll('.navegacion-principal a'); //seleccionar todos los enlaces de la navegacion principal

    // iterar sobre los enlaces caundo son varios elementos
    enlaces.forEach( function(enlace) {
        enlace.addEventListener('click', function(e) {
            e.preventDefault();
            // console.log(e.target.attributes.href.value); // regresa  #lineup

            // da la seccion a la cual le damos click
            const seccion = document.querySelector(e.target.attributes.href.value);
            
            // apicar el scroll al acceder a cada seccion
            seccion.scrollIntoView({
                behavior: 'smooth'  //dar el efecto de scroll lento
            });
        });
    });
    
};

function navegacionFija() {
    // barra de header
    const barra = document.querySelector('.header');

    //  API de intersection observer 
    // REGISRAR EL INTERSECTION OBSERVER
    // va a ver si un elemento esta visible o no
    const observer = new IntersectionObserver( function(entries) {
        if(entries[0].isIntersecting) {
            barra.classList.remove('fijo'); 
        } else {
            barra.classList.add('fijo'); //clase definida en el _header.scss
        }
    });

    // EL ELEMENTO A OBSERVAR
    observer.observe(document.querySelector('.sobre-festival'));

};